<script>
    export default {
        name: "Profile",
        data() {
            return {
                user:"",
                geohash:"",
                latitude:"",
                longitude:"",
                isShow:true,
            }
        },
        created() {
            //引入组件看用户是否已经登录
            if (localStorage.getItem('user')){
                this.isShow = true
            }else {
                this.isShow = false;
            }

            this.geohash = this.$route.query.geohash;
            // console.log(this.$route.query.geohash);
            //获取用户信息
            this.axios.get("v1/user").then(res=>{
                console.log(res)
                this.user = res;
            }).catch(err=>{})
        },
        methods:{
            take_out_food(){
                this.$router.push({
                    path:'/msite',
                    query:{
                        geohash:this.geohash,
                    }
                })
            },
            money(){
                this.$router.push({
                    path:'/balance'
                })
            },
            redPacket() {
                this.$router.push({
                    path:'/benefit'
                })
            },
            goSearch(){
                this.$router.push({
                    path:'/search'
                })
            },
            goOrder(){
                this.$router.push({
                    path:'/order'
                })
            },
            goPoints(){
                this.$router.push({
                    path:'/Points'
                })
            }

        }
    }
</script>

